<template>
    <div>
        <div v-if="loading" class="loading">Loading&#8230;</div>

        <div class="row" v-if="!loading">
            <section class="col">
                <result-match v-for="match in publicMatches" :game="match.game"></result-match>
            </section>

        </div>
    </div>
</template>

<script>
    import Match from './../Components/Match';
    import Knockout from './../Components/Knockout.vue';
    import firebaseController from '../firebaseController';
    import GroupModel from "../Model/group";
    import KnockoutModel from "../Model/knockout"
    import MatchModel from "../Model/match";
    import Modal from './../Components/Modal';
    import ResultMatch from "../Components/ResultMatch";

    export default {
        mounted() {
            firebaseController.getPublicBets().then(res => {
                let allMatches = [];
                Object.keys(res.val()).forEach(userId => {
                    const userBets = res.val()[userId]
                    Object.keys(userBets).forEach(matchId => {
                        if (new Date(userBets[matchId].game.date).getTime() < new Date().getTime() ){
                            allMatches.push({matchId: matchId, game: userBets[matchId].game});
                        }
                    })
                });

                allMatches = _.uniqBy(allMatches, 'matchId')

                allMatches.forEach(match => {
                    const matchId = match.matchId;
                    _.find(allMatches, {matchId : matchId})['tips'] = {}
                    Object.keys(res.val()).forEach(userId => {
                        const userBets = res.val()[userId];
                        if (userBets[matchId]){
                            _.find(allMatches, {matchId : matchId})['tips'][userId] = Array.isArray(userBets[matchId].bet) ?  _.without(userBets[matchId].bet, undefined, null) : userBets[matchId].bet;
                        }
                    })
                })
                // this.publicBets = allMatches;
                this.publicMatches = _.take(Object.values(allMatches).sort((a, b) => {
                    return (a.game.date < b.game.date) ? 1 : ((b.game.date < a.game.date) ? -1 : 0);
                }), 3);
                console.log(allMatches)
                this.loading = false;
            })

        },
        components: {
            ResultMatch
        },

        data() {
            return {
                publicMatches : {},

                loading: true
            }
        }
    };
</script>
