<template>
    <div class="row container">
        <div v-if="loading" class="loading">Loading&#8230;</div>

        <img class="logo" src="logo.png">
        <button v-on:click="signIn">Sign in</button>
    </div>
</template>

<script>

    import firebase from 'firebase'
    import firebaseController from '../firebaseController'
    import axios from 'axios'


    export default {
        name: "login",

        methods: {
            signIn: function () {
                this.loading = true;
                const provider = new firebase.auth.FacebookAuthProvider();
                firebase.auth().signInWithPopup(provider).then(res => {
                    // axios.get(res.additionalUserInfo.profile.picture.data.url, {
                    //     responseType: 'blob'
                    // }).then(img => {
                    //     this.upload(img.data, "userid");
                    // })
                    this.loading = false;
                    this.$router.push('/matches')
                })
            },
            upload(file, filename) {
                this.uploadTask = firebaseController.getFirebaseApp().storage().ref('profiles/' + firebaseController.getCurrentUserId()).put(file);
                this.uploadTask.then(snapshot => {
                    this.downloadURL = this.uploadTask.snapshot.downloadURL;
                    this.$emit('url', this.downloadURL)
                    firebaseController.saveUser().then(() => {
                        firebaseController.initData();
                        this.loading = false;
                        this.$router.push('/matches')
                    })
                });

            }
        },
        watch: {
            uploadTask: function () {
                this.uploadTask.on('state_changed', sp => {
                    this.progressUpload = Math.floor(sp.bytesTransferred / sp.totalBytes * 100)
                    console.log(sp)
                })
            }
        },
        data() {
            return {
                msg: 'Welcome to Your Vue.js App',
                token: null,
                loading: false
            }
        }

    }
</script>

<style scoped>
    .logo {
        height: 200px;
    }

    .container {
        display: flex;
        align-items: center;
        flex-direction: column;
        height: 95vh;
        padding-top: 25%;
    }
</style>